.App {
  margin: 0;
  padding: 0;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: black;
  margin: 3%;
}

.city-item {
  border: 2px solid black;
  width: 20%;
  margin: auto;
  margin-bottom: 3%;
  padding-left: 2%;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin-bottom: 3%;
}

.weather-desc {
  padding-bottom: 2%;
  line-height: .3em;
}

.bold {
  font-weight: bold;
  padding-bottom: 0;
}